import { request } from '@/utils/request'
export const articleListAPI = (params) => {
  return request({
    url: '/h5/interview/query',
    params
  })
}
export const articleDetailAPI = (id) => {
  return request({
    url: '/h5/interview/show',
    params: {
      id
    }
  })
}
export const articleLikedAPI = (params) => {
  return request({
    url: '/h5/interview/opt/list',
    params
  })
}
export const articleCollectedAPI = (params) => {
  return request({
    url: '/h5/interview/opt/list',
    params
  })
}
