<template>
  <div class="article-page">
    <nav class="my-nav van-hairline--bottom">
      <a :class="{ active: sorter === 'weight_desc' }" @click="changeTab('weight_desc')" href="javascript:;">推荐</a>
      <a :class="{ active: sorter == null }" @click="changeTab(null)" href="javascript:;">最新</a>
      <div class="logo"><img src="@/assets/logo.png" alt="" /></div>
    </nav>
    <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
    >
      <article-item  v-for="item in rows" :key="item.id" :item="item" @click.native="detail(item.id)"/>
    </van-list>
    <!-- 每一项 -->

  </div>
</template>

<script>
import { articleListAPI } from '@/api/article'
export default {
  name: 'article-page',
  data () {
    return {
      rows: [],
      loading: false,
      finished: false,
      page: 1,
      sorter: 'weight_desc'
    }
  },
  methods: {
    changeTab (sorter) {
      this.sorter = sorter
      // 手动把finished改为false
      this.loading = false
      this.page = 1
      this.finished = false
      this.rows = []
      this.onLoad()
    },
    detail (id) {
      this.$router.push('/detail/' + id)
    },
    async onLoad () {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      const res = await articleListAPI({
        current: this.page++,
        pageSize: 10,
        sorter: this.sorter
      })
      console.log('load调用了')
      this.rows.push(...res.data.rows)
      // 加载状态结束
      this.loading = false
      // 数据全部加载完成
      if (this.rows.length >= res.data.total) {
        this.finished = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
.article-page {
  margin-bottom: 50px;
  margin-top: 44px;
  .my-nav {
    height: 44px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 999;
    background: #fff;
    display: flex;
    align-items: center;
    > a {
      color: #999;
      font-size: 14px;
      line-height: 44px;
      margin-left: 20px;
      position: relative;
      transition: all 0.3s;
      &::after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        width: 0;
        height: 2px;
        background: #222;
        transition: all 0.3s;
      }
      &.active {
        color: #222;
        &::after {
          width: 14px;
        }
      }
    }
    .logo {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      > img {
        width: 64px;
        height: 28px;
        display: block;
        margin-right: 10px;
      }
    }
  }
}
</style>
