<template>
  <div class="collect-page">
    <van-nav-bar
        title="我的收藏"
    />
    <article-item v-for="item in items" :key="item.id" :item="item" />
  </div>
</template>

<script>
import { articleCollectedAPI } from '@/api/article'

export default {
  name: 'collect-page',
  data () {
    return {
      items: []
    }
  },
  async created () {
    const res = await articleCollectedAPI({ optType: 1, pageSize: 10, page: 1 })
    this.items = res.data.rows
  },
  methods: {
    // onClickLeft () {
    //   this.$router.back()
    // }
  }
}
</script>

