import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/views/layout'
import Login from '@/views/login'
import Register from '@/views/register'
import Detail from '@/views/detail'
import Like from '@/views/like'
import Article from '@/views/article'
import User from '@/views/user'
import Collect from '@/views/collect'
import { getToken } from '@/utils/storage'
import { Toast } from 'vant'
Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: 'myarticle' },
  {
    name: 'mblayout',
    path: '/',
    children: [
      { name: 'mylike', path: 'mylike', component: Like },
      { name: 'myarticle', path: 'myarticle', component: Article },
      { name: 'myuser', path: 'myuser', component: User },
      { name: 'mycollect', path: 'mycollect', component: Collect }
    ],
    component: Layout
  },
  { name: 'mblogin', path: '/login', component: Login },
  { name: 'mbregister', path: '/register', component: Register },
  { name: 'mbdetail', path: '/detail/:id', component: Detail }
]

const router = new VueRouter({
  routes
})
// 设置白名单
const whiteList = ['/login', '/register']
router.beforeEach((to, from, next) => {
  if (whiteList.includes(to.path)) {
    // 在白名单中的直接放行
    next()
  } else {
    // 不在白名单中的还要进行一次判断,判断是否登录了
    if (getToken()) {
      next()
    } else {
      Toast('请先登录')
      next('/login')
    }
  }
})

export default router
