<template>
  <div class="register-page">
    <van-nav-bar title="面经注册"/>
    <van-form @submit="onSubmit">
      <van-field
          v-model="user.username"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          :rules="rules.username"
      />
      <van-field
          v-model="user.password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="rules.password"
      />
      <div style="margin: 16px;">
        <van-button  block type="primary" native-type="submit">注册</van-button>
      </div>
    </van-form>
    <van-cell>
      <router-link class="link" to="/login">有账号,去登录</router-link>
    </van-cell>
  </div>
</template>

<script>
import { registerApi } from '@/api/user'
export default {
  name: 'register-page',
  data () {
    return {
      user: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '请填写账号' },
          { pattern: /^\w{5,}$/, message: '账号至少5位' }
        ],
        password: [
          { required: true, message: '请填写密码' },
          { pattern: /^\w{6,}$/, message: '密码至少6位' }
        ]
      }
    }
  },
  methods: {
    async onSubmit () {
      try {
        await registerApi(this.user)
        this.$toast.success('注册成功!')
        await this.$router.push({ name: 'mblogin' })
      } catch (e) {
      }
    }
  }
}
</script>

<style lang="less" scoped>
.link {
  color: #069;
  font-size: 12px;
  padding-right: 20px;
  float: right;
}
</style>
