<template>
  <div class="detail-page">
    <van-nav-bar
        title="面经详情"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
    />
    <article-item :item="item"></article-item>
    <div class="opt">
      <van-icon :class="{active:item.likeFlag}" name="like-o" @click="onLike" size="30" />
      <van-icon :class="{active:item.collectFlag}" name="star-o" @click="onCollect"  size="30"/>
    </div>
  </div>
</template>

<script>
import { articleDetailAPI } from '@/api/article'
import { likedAPI, collectedAPI } from '@/api/user'
export default {
  name: 'detail-page',
  data () {
    return {
      item: {}
    }
  },
  async created () {
    const res = await articleDetailAPI(this.$route.params.id)
    this.item = res.data
    console.log(this.item)
  },
  methods: {
    onClickLeft () {
      this.$router.back()
    },
    async onLike () {
      this.item.likeFlag = !this.item.likeFlag
      try {
        await likedAPI({ id: this.item.id, optType: 1 })
        if (this.item.likeFlag) {
          this.$toast.success('点赞成功')
        } else {
          this.$toast.success('取消点赞')
        }
      } catch (e) {
        this.item.likeFlag = false
        this.$toast.fail('点赞失败')
      }
    },
    async onCollect () {
      this.item.collectFlag = !this.item.collectFlag
      try {
        await collectedAPI({ id: this.item.id, optType: 2 })
        if (this.item.collectFlag) {
          this.$toast.success('收藏成功')
        } else {
          this.$toast.success('取消收藏')
        }
      } catch (e) {
        this.item.collectFlag = false
        this.$toast.fail('收藏失败')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.detail-page {
  //margin-top: 44px;
  overflow: hidden;
  padding: 0 15px;
  .header {
    h1 {
      font-size: 24px;
    }
    p {
      color: #999;
      font-size: 12px;
      display: flex;
      align-items: center;
    }
    img {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
    }
  }
  .opt {
    position: fixed;
    bottom: 100px;
    right: 0;
    > .van-icon {
      margin-right: 20px;
      background: #fff;
      width: 40px;
      height: 40px;
      line-height: 40px;
      text-align: center;
      border-radius: 50%;
      box-shadow: 2px 2px 10px #ccc;
      font-size: 18px;
      &.active {
        background: #FEC635;
        color: #fff;
      }
    }
  }
}
</style>
