import { request } from '@/utils/request'

export const registerApi = (data) => {
  return request({
    url: '/h5/user/register',
    method: 'post',
    data
  })
}
export const loginApi = (data) => {
  return request({
    url: '/h5/user/login',
    method: 'post',
    data
  })
}
export const likedAPI = (data) => {
  return request({
    url: '/h5/interview/opt',
    method: 'post',
    data
  })
}
export const collectedAPI = (data) => {
  return request({
    url: '/h5/interview/opt',
    method: 'post',
    data
  })
}
export const currentUserAPI = () => {
  return request({
    url: '/h5/user/currentUser'
  })
}
