<template>
  <div class="like-page">
    <van-nav-bar
        title="我的点赞"
    />
    <article-item v-for="item in items" :key="item.id" :item="item" />
  </div>
</template>

<script>
import { articleLikedAPI } from '@/api/article'
export default {
  name: 'like-page',
  data () {
    return {
      items: []
    }
  },
  async created () {
    const res = await articleLikedAPI({ optType: 1, pageSize: 10, page: 1 })
    this.items = res.data.rows
  },
  methods: {
    // onClickLeft () {
    //   this.$router.back()
    // }
  }
}
</script>

